#App2 {
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.video-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  margin-bottom: 20px;
}

.video-table th,
.video-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

.video-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.video-table td.video-thumbnail {
  width: 15%;
}

.video-table td.video-title {
  width: 45%;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.video-table td.video-subscribers,
.video-table td.video-views,
.video-table td.video-contribution {
  width: 15%;
}

#검색창 이쁘게


.search-bar {
  display: flex;
  align-items: center;
  margin-top: 1rem;
}

.search-input {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 1rem;
  margin-right: 0.5rem;
}

.search-button {
  background-color: #3c3d3f;
  color: #fff;
  border: none;
  padding: 0.5rem;
  border-radius: 3px;
  font-size: 1rem;
  cursor: pointer;
}
